body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container, .container-big {
  max-width: 1024px;
  width: 100%;
  margin: 0 auto;
}
.container-big { max-width: 1200px; }

/* FLEX */
.flex {
  display: flex;
}
.horizontal, .horizontal-only { flex-direction: row; }
.vertical, .vertical-horizontal { flex-direction: column; }
@media screen and (max-width: 720px){
  .horizontal { flex-direction: column; }
  .vertical-horizontal { flex-direction: row; }
}

.flex-1 { flex: 1 1; }
.flex-2 { flex: 2 1; }
.flex-3 { flex: 3 1; }
.flex-4 { flex: 4 1; }
.flex-5 { flex: 5 1; }
.flex-6 { flex: 6 1; }
.flex-7 { flex: 7 1; }
.flex-8 { flex: 8 1; }
.flex-9 { flex: 9 1; }
.flex-10 { flex: 10 1; }

.flex-center {
  justify-content: center;
  align-items: center;
}
.flex-start {
  justify-content: flex-start;
  align-items: center;
}

/* FONT */
.text-center {
  text-align: center;
}

/* COMPONENTS */
.TopBar {
  background-color: #2b2b3d;
  font-size: 0.75rem;
  padding: 5px;
  color: #FEFEFE;
}

.NavBar {
  height: 100px;
  padding: 10px 0;
}
.form-input {
  padding: 10px 15px;
  margin: 5px;
  border: 1px solid #777;
  border-radius: 10px;
  font-size: 0.95rem;
}
.form-input[name="pesquisa"] {
  width: 60%;
}
.color-red {
  color: #c40505;
}
.color-white {
  color: #fefefe;
}
.link {
  cursor: pointer;
}

.Categorias {
  background-color: #3a3a4d;
  padding: 10px 0;
  color: rgb(240, 240, 240);
}
.Categorias > div > div {
  padding: 5px;
  cursor: pointer;
}
.Categorias > div > div:hover {
  background-color: #5e5e68;
  border-radius: 10px;
}

.titulo {
  margin: 10px 0;
}

.set-arrow {
  background-color: #5e5e68;
  width: 40px;
  height: 25px;
  border-radius: 100px;
  padding: 7px 0;
  text-align: center;
  font-size: 1.2rem;
  color: #FEFEFE;
  cursor: pointer;
}

.star-container {
  background-color: #454551;
  padding: 10px 18px;
  border-radius: 20px;
}

.pic-main {
  height: 40vh;
}

.painel {  
  border: 1px solid #aaa;
  border-radius: 10px;
}
.painel-head {
  border-bottom: 1px solid #aaa; 
}

.painel-head > div > div {
  text-align: center;
  padding: 15px;
  border-right: 1px solid #aaa;
}
.data-active {
  /* background-color: #d8d8d8; */
  background-color: rgb(190, 7, 7);
  color: #FEFEFE;
}
.painel-head > .top > div:first-child {
  border-top-left-radius: 8px;
}
.painel-head > .bottom > div:last-child {
  border-right: 0;
  border-top-right-radius: 8px;
}
.painel-body {
  padding: 20px;
}

.price {
  font-size: 3rem;
  margin : 20px 0 0 0;
  color: #171717;
}
.price > span {
  font-size: 1rem;
}
.price-base {
  color: #343434;
}

.button-cta {
  background-color: #cd1616;
  color: #FEFEFE;
  padding: 15px 20px;
  width: 100%;
  font-size: 1.5rem;
  border: 0;
  cursor: pointer;
}
.button-cta:hover {
  background-color: #b30f0f;
}

.Avaliacoes {
  padding: 30px 0;
  background-color: rgb(46, 58, 79);
  color: #FEFEFE;
}
.card {
  background-color: #fefefe;
  color: #2b2b3d;
  margin: 10px;
  padding: 15px;
  border-radius: 15px;
}
.card-body {
  padding: 10px 0;
}
.card-head {
  float: right;
}

/* COMPRAR JUNTO */
.ComprarJunto {
  padding: 15px;
}
.pic-old {
  width: 150px;
  height: 150px;
}

/* RECOMENDACOES */
.Recomendacoes {
  padding: 20px;
}
.padding-15 {
  padding: 15px;
}
.plus-sign {
  padding: 15px;
  font-size: 1.3rem;
}
.preco-cp {
  font-size: 1.2rem;
  color: #05610b;
  font-weight: bold;
}
.button-cp {
  margin: 10px;
  padding: 12px 18px;
  background-color: #c70f0f;
  height: 50px;
  font-size: 1.2rem;
  color: #FEFEFE;
  border: 0;
  cursor: pointer;
}
.button-cp:hover {
  background-color: #890909;
}
.RedesSociais {
  background-color: #1d1d3b;
  padding: 25px 0;
  color: #fefefe;
}

.Contatos, .Institucional {
  padding: 30px;
}
.Contatos {
  color: #2f2e36;
}

.Institucional {
  background-color: #2f2e36;
  color: #FEFEFE;
}

.Footer {
  background-color: #222;
  color: #FEFEFE;
  font-size: 1rem;
  padding: 10px 0;
}

.mobile-only {
  display: none;
}

/* MOBILE */
@media screen and (max-width: 720px){
  .no-mobile {
    display: none;
  }

  .mobile-padding-10 {
    padding: 10px;
  }

  .NavBar {
    height: 130px;
  }

  .text-center-mb {
    text-align: center;
  }

  .Categorias {
    width: 100%;
    overflow-x: scroll;
  }

  .painel {
    margin: 10px;
  }
  .painel-head > div {
    padding: 0;
    border-right: 0px;
  }
  .painel-head > div > div {
    padding: 15px;
  }
  .painel-head > .top > div {
    border-bottom: 1px solid #777;
  }
  .painel-head > .top > div:first-child {
    border-top-left-radius: 8px;
  }
  .painel-head > .top > div:nth-child(2) {
    border-right: 0;
    border-top-right-radius: 8px;
  }
  .painel-head > .bottom > div:nth-child(2) {
    border-right: 0;
    border-top-right-radius: 0;
  }
  .painel-body {
    padding: 20px;
  }

  .button-cta {
    width: calc(100% - 10px);
    margin: 0 5px;
  }

  .pic-main {
    height: auto;
    width: 80%;
    margin: 10px 0;
  }

  .mobile-only {
    display: flex;
  }

}
